  transform: translate3d(0, 0, 0);
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.hamburger.is-closed {
  position: fixed;
}
.hamburger.is-closed:hover::before {
  transform: translate3d(-100px, 0, 0);
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.hamburger.is-closed .hamb-top {
  top: 5px;
}
.hamburger.is-closed .hamb-middle {
  margin-top: -2px;
}
.hamburger.is-closed .hamb-bottom {
  bottom: 5px;
}
.hamburger.is-open {
  position: fixed;
}
.hamburger.is-open .hamb-top {
  margin-top: -2px;
  transform: rotate(45deg);
  -webkit-transition-timing-function: -webkit-transform 1s cubic-bezier(0.73, 1, 0.28, 0.08);
  -moz-transition-timing-function: -moz-transform 1s cubic-bezier(0.73, 1, 0.28, 0.08);
  -o-transition-timing-function: -o-transform 1s cubic-bezier(0.73, 1, 0.28, 0.08);
  transition-timing-function: transform 1s cubic-bezier(0.73, 1, 0.28, 0.08);
}
.hamburger.is-open .hamb-bottom {
